  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.flex-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
      align-items: center;
}
.amap {
  top: 0;
}
